body {
    margin: 0;
    padding: 0;
    background-color: #f5699d;
    /* infinite:무한반복, alternate:동작교차 */
    animation: bgChange 3s infinite alternate;
       /* display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; */
}
@keyframes bgChange {
    0% {
        background-color: #f5699d;
    }
    50% {
        background-color: #fff;
    }
    100% {
        background-color: #f5699d;
    }
}

.center-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.center-container a img {
    /* 가변되는 장치 사이즈에서 100% 차지하여 가변됨 : 반응형 사이트 제작시 사용 */
    max-width: 100%;
    
}